.popup__container-tooltip {
  padding: 60px 36px;
  max-width: 394px;
  width: 100%;
  background: #ffffff;
  border-radius: 10px;
  position: relative;
  margin: 0 auto;
  z-index: 4;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (max-width: 767px) {
  .popup__container-tooltip {
    padding: 50px 18px;
    max-width: 244px;
  }
}
